/* keyboard.css */
.keyboard {
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textcontainer {
    width: 1018px;
    height: 100px;
    border: 1px solid rgba(0,0,0,0.25);
    overflow: auto;
    border-radius: 4px;
}

.textcontainer pre{
    text-align: left;
    display: block;
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    margin: 0;
    padding: 20px;
    font-size: 20px;
}

.keyboardcontainer {
    width: 90%;
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.container {
    padding: 20px 20px;
    display: flex;
    flex-direction: column;
    max-width: 580px;
    height:280px;
    justify-content: center;
    align-items: center;
    border-radius: 7px;
    background: #9498a1;
}

.row {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    
}

.key {
    display: flex;
    width: 50px;
    height: 50px;
    margin: 4px;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #0f040498;
    border-radius: 4px;
    color: #ffffff;
    font-size: 16px;
    cursor: pointer;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: background-color ease-in-out 0.15s;
}

.key:hover{
    background-color: #5c6970;
}

.row:first-child.key:first-child{
    width: 40px;
}

.row:first-child .key:last-child{
    width: 80px;
}

.row:nth-child(3) .key:first-child{
    width: 95px;
}
.row:nth-child(3) .key:last-child{
    width: 95px;
}
.row:nth-child(4) .key:first-child{
    width: 130px;
}
.row:nth-child(4) .key:last-child{
    width: 130px;
}
.row:nth-child(5) .key:nth-child(3){
    width: 300px;
}
